import React, {Component} from 'react';
import {library} from '@fortawesome/fontawesome-svg-core'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faBroom, faDollarSign, faShoppingCart} from '@fortawesome/free-solid-svg-icons'
import CartItem from "./CartItem";
import {PageHeading} from "../util/ToolBox";
import {newearGrey, newearPurple} from "../styles/colorPalette";
import {NavLink} from "react-router-dom";

library.add(faShoppingCart, faDollarSign, faBroom);

const costPerConcert = 25;

const festivalLightsSrc = "http://newear.org/wp-content/uploads/2016/08/newEar25.png";

const styles = {
    background: {
        backgroundImage: `url(${festivalLightsSrc})`,
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
    },
    text: {
        backgroundColor: 'white',
        color: {newearPurple},
        opacity: 0.95
    },
    items: {
        backgroundColor: {newearGrey}
    }
};

class Cart extends Component {
    constructor(props) {
        super(props);
        this.cartMsg = this.cartMsg.bind(this);
        this.getTotalCost = this.getTotalCost.bind(this);
    }

    checkout() {
        this.props.cartItems.map((item) => item.purchase());
    }

    cartMsg(username) {
        const suffix = (username) ? username + "'s " : "";
        return suffix + "cart"
    }

    getTotalCost() {
        const concertCount = this.props.cartItems.filter(item => !item.isPurchased)
            .map(item => item.itemQuantity)
            .reduce((accumulator, quantity) => accumulator + quantity, 0);
        return concertCount * costPerConcert;

    }

    render() {
        const items = Array.from(this.props.cartItems);
        return (
            <div className="col m-2 p-3">
                <PageHeading title={this.cartMsg(this.props.username)}/>
                <div className="row rounded align-items-center justify-content-around m-3 p-3"
                     style={styles.background}>
                    <div className="col rounded" style={styles.text}>
                        <div className="row m-3 p-2 ">
                            <div className="col-md-3 rounded text-center m-2" style={styles.text}>
                                amount due: ${this.getTotalCost()}
                            </div>
                            <div className="col-md-1"></div>
                            <div className="col-md-3 m-2">
                                <button className="btn btn-primary col" onClick={this.props.checkout}>
                                    <FontAwesomeIcon icon="dollar-sign"/> checkout
                                </button>
                            </div>
                            <div className="col-md-3 m-2">
                                <button className="btn btn-primary col" onClick={this.props.clearCart}>
                                    <FontAwesomeIcon icon="broom"/> clear cart
                                </button>
                            </div>
                        </div>
                        <div className="row m-3 p-2">
                            <ul className="list-group col ml-sm-3" style={styles.items}>
                                {items.map(((item) => (
                                    <li className="list-group-item row" key={Math.random()}>
                                        <CartItem itemId={item.itemId}
                                                  itemName={item.itemName}
                                                  itemQuantity={item.itemQuantity}
                                                  isPurchased={item.isPurchased}
                                        />
                                        <div className="col-md-3 m-3">
                                            <button className="btn btn-primary"
                                                    onClick={(event) => this.props.removeItem(event, item.itemName)}>
                                                remove
                                            </button>
                                        </div>
                                    </li>
                                )))}
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

function MiniCart(props) {
    return (
        <li className="col nav-link mb-5">
            <NavLink to="/cart">{/* className="col ml-3">*/}
                <div>{props.cartItems.length}</div>
                <FontAwesomeIcon icon="shopping-cart"/>
            </NavLink>
        </li>
    );
}


export {Cart, MiniCart};