import React, {Component} from 'react';
import {NavLink} from "react-router-dom";


const newEarLogo = "http://newear.org/wp-content/uploads/2015/10/newEar-Logo-RGB-Red-DkRed.png";

const styles = {
    logo: {
        width: '21em',
        height: 'auto'
    }
};

function showLogin(props) {
    if (!props.isLoggedIn) {
        return (
            <div className="col">
                <div className="row">
                    <div className="col m-3">
                        <label className="">Username</label>
                        <input type="text"
                               name="username"
                               className="form-control"
                               placeholder="username"
                               value={props.username}
                               onChange={props.handleInputChange}
                        />

                    </div>
                    <div className="col m-3">
                        <label className="">Password</label>
                        <input type="password"
                               name="password"
                               className="form-control"
                               placeholder="password"
                               value={props.password}
                               onChange={props.handleInputChange}
                        />
                    </div>
                </div>
            </div>
        );
    } else {
        return (
            <div className="col"/>
        );
    }
}

function showSignUp(isLoggedIn) {
    if (isLoggedIn !== true) {
        return (
            <div className="col m-3">
                <NavLink className="btn btn-primary p-3" to="/sign-up">
                    sign-up
                </NavLink>
            </div>
        );
    }
}

class LoginBar extends Component {
    render() {
        const isLoggedIn = this.props.isLoggedIn;
        let loginOrOut = isLoggedIn ? "Log out" : "Log in";
        return (
            <div className="row">
                <div className="col">
                    <img src={newEarLogo} alt="newEar logo" style={styles.logo}/>
                </div>
                {showLogin(this.props)}
                <div className="col">
                    <div className="row p-2">
                        {showSignUp(this.props.isLoggedIn)}
                        <div className="col m-3">
                            <button className="btn btn-primary p-3"
                                    onClick={this.props.handleLogin}>
                                {loginOrOut}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default LoginBar;