import React, {Component} from 'react';
import ScheduleBlock from './ScheduleBlock';
import 'bootstrap/dist/css/bootstrap.css';
import {newearGrey} from "../../styles/colorPalette";

const styles = {
    details: {
        fontSize: '.85em',
    },
    blocks: {
        background: `${newearGrey}`,
        opacity: 0.96
    }
};

const mapScheduledBlockToLi = (block) => (
    <li className="list-group-item m-1 row" key={block.blockId} style={styles.blocks}>
        <ScheduleBlock
            name={block.name}
            scheduledTime={block.scheduledTime}
        />
    </li>
);

class Schedule extends Component {
    constructor(props) {
        super(props);
        this.state = {scheduledBlocks: props.scheduledBlocks};
    }

    render() {
        const blocks = Array.from(this.state.scheduledBlocks);
        return (
            <div className="col p-2">
                <div className="row text-center m-3">
                    <h2 className="col">Festival Schedule</h2>
                </div>
                <ul className="list-group">
                    {blocks.map(mapScheduledBlockToLi)}
                </ul>
            </div>
        );
    }
}

export default Schedule;