import React, {Component} from 'react';
import Concert from './Concert';
import 'bootstrap/dist/css/bootstrap.css';
import {PageHeading} from '../util/ToolBox';


class Concerts extends Component {

    constructor(props) {
        super(props);
        this.state = {
            concerts: props.concerts
        }
        this.mapConcertToLi = this.mapConcertToLi.bind(this);
    }

    mapConcertToLi(concert) {
        return (
            <li className="list-group-item col" key={concert.concertId}>
                <Concert
                    concertId={concert.concertId}
                    concertTitle={concert.concertTitle}
                    posterLink={concert.posterLink}
                    posterAltText={concert.posterAltText}
                    concertDescription={concert.concertDescription}
                    pieces={concert.pieces}
                    addToCart={this.props.addToCart}
                />
            </li>
        );
    }

    render() {
        const concerts = Array.from(this.state.concerts);
        return (<div className="row">
            <div className="col">
                <PageHeading title="Season 26: The Human Experience"/>
                <ul className="row p-0">
                    {concerts.map((concert) => (
                            <li className="list-group-item" key={concert.concertId}>
                                <Concert
                                    concertId={concert.concertId}
                                    concertTitle={concert.concertTitle}
                                    posterLink={concert.posterLink}
                                    posterAltText={concert.posterAltText}
                                    concertDescription={concert.concertDescription}
                                    pieces={concert.pieces}
                                    addToCart={this.props.addToCart}
                                    cartItems={this.props.cartItems}
                                />
                            </li>
                        )
                    )}
                </ul>
            </div>
        </div>);
    }


}

export default Concerts;