import React, {Component} from 'react';
import {HashRouter, NavLink, Route} from "react-router-dom";
import About from './about/About';
import Home from './home/Home';
import Concerts from './concert/Concerts';
import currentConcerts from './config/ConcertConfig';
import Kccmf from './kccmf/Kccmf';
import News from './news/News';
import {Cart, MiniCart} from "./cart/Cart";
import SignUp from './login/SignUp';
import LoginBar from './login/LoginBar';
import Footer from './footer/Footer'
import {Donate, DonateButton} from "./donations/Donations";
import {
    aboutRoute,
    cartRoute,
    concertsRoute,
    donationsRoute,
    homeRoute,
    kccmfRoute,
    newsRoute,
    signUpRoute
} from './config/Routes';
import {addConcert, checkout, clearAll, isAUser, loadCart, removeItem} from './Firebase';
import 'bootstrap/dist/css/bootstrap.css';
import './App.css';
import './styles/newear.scss';

class NewEar extends Component {

    constructor(props) {
        super(props);
        this.state = {
            currentView: <Kccmf className="row"/>,
            username: '',
            email: '',
            password: '',
            isLoggedIn: false,
            cartItems: []
        };
        this.handleClick = this.handleClick.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleAddToCart = this.handleAddToCart.bind(this);
        this.handleClearCart = this.handleClearCart.bind(this);
        this.handleCheckout = this.handleCheckout.bind(this);
        this.handleRemoveItem = this.handleRemoveItem.bind(this);
    }

    handleClick(props) {
        this.setState({
            currentView: props.nextView
        });
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    async handleLogin(event) {
        event.preventDefault();
        if (!this.state.isLoggedIn) {
            const validUser = await isAUser(this.state.username, this.state.password);
            if (validUser) {
                const usersCart = await loadCart(this.state.username);
                this.setState({isLoggedIn: true, cartItems: usersCart});
            } else {
                alert("invalid username and password combination. Have you created an account?")
            }
        } else {
            this.setState({username: '', password: '', isLoggedIn: false, cartItems: []});
        }
        console.log("The user " + this.state.username + " is logged in: " + this.state.isLoggedIn);
    }

    async handleAddToCart(event, itemId, itemName) {
        event.preventDefault();
        const newCartItems = await addConcert(this.state.username, itemId, itemName,
            1, false);
        this.setState({
            cartItems: newCartItems
        });
    }

    async handleClearCart(event) {
        event.preventDefault();
        const items = this.state.cartItems;
        if (this.state.isLoggedIn || (Array.isArray(items) && items.length > 0)) {
            this.setState({cartItems: await clearAll(this.state.username)});
        }
    }

    async handleCheckout(event) {
        event.preventDefault();
        const newCartItems = await checkout(this.state.username);
        this.setState({
            cartItems: newCartItems
        });
        if (Array.isArray(newCartItems) && newCartItems.length > 0) {
            alert("Thanks for your purchase! See you at the show!");
        } else {
            alert("It's awesome you want to purchase some tickets, but you have to pick out your concerts first. " +
                "Head on over to /concerts to explore our current season.");
        }
    }

    async handleRemoveItem(event, itemName) {
        event.preventDefault();
        const newCartItems = await removeItem(this.state.username, itemName);
        this.setState({
            cartItems: newCartItems
        });
    }

    render() {
        return (
            <HashRouter>
                <div className="App container-fluid">
                    <div className="row">
                        <div className="col header rounded align-items-center justify-content-around">
                            <LoginBar username={this.state.username}
                                      password={this.state.password}
                                      isLoggedIn={this.state.isLoggedIn}
                                      handleInputChange={this.handleInputChange.bind(this)}
                                      handleLogin={this.handleLogin.bind(this)}/>
                            <div className="row">
                                <nav
                                    className="navbar-expand col"
                                    role="navigation">
                                    <ul className="navbar-collapse row">
                                        <div className="navbar-nav col">
                                            <div className="row">
                                                <li className="col nav-link">
                                                    <NavLink exact to="/">Home</NavLink>
                                                </li>
                                                <li className="col nav-link">
                                                    <NavLink to="/concerts">concerts</NavLink>
                                                </li>
                                                <li className="col nav-link">
                                                    <NavLink to="/about">about</NavLink>
                                                </li>
                                                <li className="col nav-link">
                                                    <NavLink to="/news">news</NavLink>
                                                </li>
                                                <li className="col nav-link">
                                                    <NavLink to="/kccmf">kccmf</NavLink>
                                                </li>
                                                <MiniCart cartItems={this.state.cartItems}/>
                                                <DonateButton/>
                                            </div>
                                        </div>
                                    </ul>
                                </nav>
                            </div>
                        </div>
                    </div>
                    <div className="content row">
                        <Route exact path={homeRoute} component={Home}/>
                        <Route path={concertsRoute}
                               render={(props) => <Concerts {...props}
                                                            concerts={currentConcerts}
                                                            cartItems={this.state.cartItems}
                                                            addToCart={this.handleAddToCart.bind(this)}
                               />}
                        />
                        <Route path={aboutRoute} component={About}/>
                        <Route path={newsRoute} component={News}/>
                        <Route path={kccmfRoute} component={Kccmf}/>
                        <Route path={signUpRoute}
                               render={(props) => <SignUp {...props}
                                                          username={this.state.username}
                                                          email={this.state.email}
                                                          password={this.state.password}
                               />}
                        />
                        <Route path={cartRoute}
                               render={(props) => <Cart {...props}
                                                        username={this.state.username}
                                                        isLoggedIn={this.state.isLoggedIn}
                                                        cartItems={this.state.cartItems}
                                                        clearCart={this.handleClearCart.bind(this)}
                                                        checkout={this.handleCheckout.bind(this)}
                                                        removeItem={this.handleRemoveItem.bind(this)}
                               />}
                        />
                        <Route path={donationsRoute} component={Donate}/>
                    </div>
                    <Footer/>
                </div>
            </HashRouter>
        );
    }
}

export default NewEar;