const usersPath = "users";

const emptyCart = [];

// determines if the username/password combo represents a signed up user
// this function compares the passwords client-side which is unsafe
// and would never be used in production
async function isAUser(username, password) {
    const userRef = getUserRef(username);
    return await userRef && userRef.get()
        .then((doc) => {
            return doc.exists && doc.data().password === password;
        })
        .catch((error) =>
            console.error("Error validating user: ", error)
        );
}

async function addUser(username, email, password) {
    return await getUserRef(username)
        .set({
                username: username,
                email: email,
                password: password,
                cartItems: emptyCart
            },
            {merge: true}
        )
        .then(function () {
            console.log("User successfully added:", username, email);
        })
        .catch(function (error) {
            console.error("Error writing document: ", error);
        });
}

async function loadCart(username) {
    const userRef = getUserRef(username);
    return await userRef && userRef.get()
        .then((doc) => {
            if (doc.exists) {
                const items = doc.data().cartItems;
                return items ? items : emptyCart;
            } else {
                return emptyCart;
            }
        })
        .catch((error) =>
            console.error("Error loading cart: ", error)
        );
}

async function addConcert(username, itemId, itemName, itemQuantity, isPurchased) {
    if (!username) {
        alert("Looks like you'd like to add this to the cart. First, log in or sign up.");
        return emptyCart;
    }
    const userRef = getUserRef(username);
    if (userRef) {
        return await addItem(userRef, itemId, itemName, itemQuantity, isPurchased, username);
    } else {
        return emptyCart;
    }
}

async function addItem(userRef, itemId, itemName, itemQuantity, isPurchased, username) {
    return await userRef.get()
        .then((doc) => {
            const oldItems = doc.data().cartItems;
            // if item is new add it,  otherwise increment the itemQuantity
            let newItems = pushItem(oldItems, itemId, itemName, itemQuantity, isPurchased);
            userRef.set({cartItems: newItems}, {merge: true})
                .then(function () {
                    console.log("cartItem successfully added:", username, newItems);
                })
                .catch(function (error) {
                    console.error("Error adding cartItem: ", error);
                });
            return newItems;
        })
        .catch(function (error) {
            console.error("Error adding cartItem: ", error);
        });
}

function pushItem(oldItems, itemId, itemName, itemQuantity, isPurchased) {
    let newItems = oldItems ? oldItems : emptyCart;
    let isUpdate = false;
    newItems.map(item => {
        if (itemName === item.itemName) {
            item.itemQuantity = item.itemQuantity + 1;
            isUpdate = true;
        }
        return item;
    });
    // if isUpdate, return updated list, otherwise concat new item
    return isUpdate
        ? newItems
        : newItems.concat({
            itemId: itemId,
            itemName: itemName,
            itemQuantity: itemQuantity,
            isPurchased: isPurchased
        });
}

const purchaseError = (e) => console.log("error while purchasing cart: ", e);

async function checkout(username) {
    if (username) {
        const userRef = getUserRef(username);
        if (userRef) {
            const purchasedItems = await userRef.get()
                .then((doc) => {
                    return purchaseAll(doc);
                })
                .catch(purchaseError);
            return await userRef.set({cartItems: purchasedItems}, {merge: true})
                .then(() => {
                    console.log("Checked out cart for userRef: ", userRef);
                })
                .then(() => purchasedItems)
                .catch(purchaseError);
        }
    }
    return emptyCart;
}

function purchaseAll(doc) {
    if (doc.exists) {
        const oldItems = doc.data().cartItems;
        if(Array.isArray(oldItems) && oldItems.length > 0) {
            return oldItems.map((item) => {
                item.isPurchased = true;
                return item;
            });
        }
    }
    return emptyCart;
}

async function clearAll(username) {
    if (username) {
        const userRef = getUserRef(username);
        if (userRef) {
            await userRef.set({cartItems: emptyCart}, {merge: true})
                .then(() => {
                    console.log("Emptied cart for userRef: ", userRef);
                })
                .catch((e) => console.log("error while clearing cart: ", e));
        }
    }
    return emptyCart;
}

async function removeItem(username, itemName) {
    if (username) {
        const userRef = getUserRef(username);
        if (userRef) {
            const newItems = await userRef.get()
                .then((doc) => {
                    const oldItems = doc.data().cartItems;
                    let newItems = oldItems ? oldItems : [];
                    return newItems.filter(item => item.itemName !== itemName);
                })
                .catch((e) => console.log("error while clearing cart: ", e));
            userRef.set({cartItems: newItems}, {merge: true})
                .then(function () {
                    console.log("cartItem successfully removed:", username, newItems, itemName);
                })
                .catch(function (error) {
                    console.error("Error removing cartItem: ", error, itemName);
                });
            return newItems;
        }
    }
    return emptyCart;
}

function getUserRef(username) {
    const db = firebase.firestore();
    return db.collection(usersPath).doc(username);
}

export {firebase, isAUser, addUser, addConcert, clearAll, checkout, loadCart, removeItem};