import React, {Component} from 'react';
import {Artist} from './Artist';
import 'bootstrap/dist/css/bootstrap.css';

const mapArtistToLi = (artist) => (
    <li className="list-group-item" key={artist.artistId}>
        <Artist
            name={artist.name}
            srcLink={artist.srcLink}
            altText={artist.altText}
            artistDescription={artist.artistDescription}
            hyperLink={artist.hyperLink}
            linkText={artist.linkText}
            classes={artist.classNames}
        />
    </li>
);

class FeaturedArtists extends Component {
    constructor(props) {
        super(props);
        this.state = {artists: props.artists};
    }

    render() {
        const artists = Array.from(this.state.artists);
        return (
            <div className="col p-2">
                <div className="row">
                    <h3 className="col text-center">Featured Artists</h3>
                </div>
                <ul className="list-group">
                    {artists.map(mapArtistToLi)}
                </ul>
            </div>
        );
    }
}

export default FeaturedArtists;
