import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import {PageHeading} from '../util/ToolBox';

const ensembleImageSrc = "http://newear.org/wp-content/uploads/2019/05/ensemble-faded-1.jpg";

const styles = {
    background: {
        backgroundImage: `url(${ensembleImageSrc})`,
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
    },
    paragraph: {
        backgroundColor: 'white',
        opacity: 0.95
    }
};

function Paragraph(props) {
    return (
        <div className="row m-4">
            <div className="col p-3 rounded align-content-center" style={styles.paragraph}>
                <p>{props.text}</p>
            </div>
        </div>
    );
}

function About(props) {
    return (
        <div className="col">
            <PageHeading title="Our Story"/>
            <div className="row" style={styles.background}>
                <div className="col">
                    <Paragraph text="newEar is a professional chamber music ensemble featuring the music of living composers."/>
                    <Paragraph text="We program a wide range of composers, instrumentations, and styles— from a traditional piano trio or
                        string quartet, to works for solo instrument and electronics, guzheng and woodwinds, or electric
                        guitar and percussion."/>
                    <Paragraph text="Our concerts often blur the boundaries of classical art music, rock, jazz, world music, and the
                        avant-garde. Other artistic disciplines are often part of newEar performances — you may see dance,
                        visual arts, or theatre in the mix."/>
                    <Paragraph text="We strive to bring Kansas City the most eclectic and cutting-edge creative trends of the 21st
                        century. You will see and hear some of the best musicians in our area interpreting music written
                        within the last 20 years, never before heard in the region or sometimes anywhere in the
                        world."/>
                    <Paragraph text="newEar has been the leading contemporary chamber ensemble in Kansas City for over 26 years. We
                        invite you to join us, listen, and experience the music of our time."/>
                </div>
            </div>
        </div>
    );
}

export default About;