import React, {Component} from 'react';
import {PageHeading} from "../util/ToolBox";
import {donationsRoute} from "../config/Routes";
import {newearPurple} from "../styles/colorPalette";
import {NavLink} from "react-router-dom";

const styles = {
    text: {
        color: `${newearPurple}`
    }
};

class Donate extends Component {

    constructor(props) {
        super(props);
        this.state = {
            benefactor: '',
            amount: 0
        };
        this.makeDonation = this.makeDonation.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    makeDonation(event) {
        event.preventDefault();
        const name = this.state.benefactor ? this.state.benefactor + "'s" : "the";
        alert("On behalf of all of us at newEar, we are grateful for "
            + name + " generous gift of $" + this.state.amount
            + ". Thank you.");
        //window.location.href = {homeRoute};
    }

    render() {
        return (
            <div className="col">
                <PageHeading title="Donate"/>
                <div className="row" style={styles.text}>
                    <div className="col">
                        <div className="row">
                            <div className="col m-2 p-2">
                                <p>newEar is a registered 501(c)3 non-profit.</p>
                                <p>Your donation will help your ensemble continue to bring the
                                    best of today's classical music to you, right here in Kansas City.</p>
                            </div>
                        </div>
                        <div className="row">
                            <form className="col"
                                  id="form-signup"
                                  onSubmit={this.makeDonation}>
                                <div className="p-2">
                                    <label>name</label>
                                    <input type="text"
                                           name="benefactor"
                                           className="form-control"
                                           placeholder="Enter in whose name this gift is being made"
                                           value={this.state.benefactor}
                                           onChange={this.handleInputChange}
                                    />
                                </div>
                                <div className="p-2">
                                    <label>amount</label>
                                    <input type="number"
                                           name="amount"
                                           className="form-control"
                                           aria-describedby="emailHelp"
                                           placeholder="Enter the amount you'd like to donate"
                                           min="0.00"
                                           step="1"
                                           value={this.state.amount}
                                           onChange={this.handleInputChange}
                                    />
                                </div>
                                <div className="p-2">
                                    <input type="submit"
                                           value="submit"
                                           className="btn btn-primary"
                                    />
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

function DonateButton() {
    return (
        <li className="col  mb-5 nav-link">
            <NavLink to="/donations" className="btn btn-primary">
                Donate
            </NavLink>
        </li>
    );
}

export {Donate, DonateButton};

