/*
 *  Defines the Artist Component and its subcomponents.
 */
import React, {Component} from 'react';

function ArtistName(props) {
    return (
        <div className="row justify-content-center">
            <h4 className="col text-center m-3">{props.name}</h4>
        </div>
    );
}


class ArtistImage extends Component {
    constructor(props) {
        super(props);
        this.state = {srcLink: props.srcLink, altText: props.altText};
    }

    render() {
        return (
            <img className="card-img"
                 src={this.state.srcLink}
                 alt={this.state.altText}/>
        );
    }
}

function ArtistDescription(props) {
    return (
        <div className="row">
            <p className="col">{props.artistDescription}</p>
        </div>
    );
}

function LinkTo(props) {
    return (
        <div className="row justify-content-center">
            <a href={props.hyperLink} className="btn btn-primary m-3 col">{
                props.linkText}
            </a>
        </div>
    );
}

class Artist extends Component {
    constructor(props) {
        super(props);
        this.state = {
            artistId: props.artistId,
            name: props.name,
            srcLink: props.srcLink,
            altText: props.altText,
            artistDescription: props.artistDescription,
            hyperLink: props.hyperLink,
            linkText: props.linkText
        };
    }

    render() {
        return (
            <div className="row justify-content-center">
                <div className="col-md-4">
                    <ArtistImage
                        srcLink={this.state.srcLink}
                        altText={this.state.altText}
                    />
                    <LinkTo
                        hyperLink={this.state.hyperLink}
                        linkText={this.state.linkText}
                    />
                </div>
                <div className="col-md-8 ">
                    <div className="card-title">
                        <ArtistName name={this.state.name}/>
                    </div>
                    <ArtistDescription artistDescription={this.state.artistDescription}/>
                </div>
            </div>
        );
    }
}

export {Artist, ArtistName};