import React, {Component} from 'react';
import {addUser} from '../Firebase';
import {concertsRoute} from "../config/Routes";

class SignUp extends Component {

    constructor(props) {
        super(props);
        this.state = {
            username: props.username,
            email: props.email,
            password: props.password
        }
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
    }


    handleInputChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    async handleSubmit(event) {
        event.preventDefault();
        await addUser(this.state.username, this.state.email, this.state.password);
        alert("Welcome " + this.state.username + "! Thanks for creating an account.");
        //window.location.href = {concertsRoute};
    }

    render() {
        return (
            <div className="col">
                <h2>Create an account</h2>
                <form id="form-signup"
                      onSubmit={this.handleSubmit}>
                    <div className="p-2">
                        <label>username</label>
                        <input type="text"
                               name="username"
                               className="form-control"
                               placeholder="Enter your username"
                               value={this.state.username}
                               onChange={this.handleInputChange}
                        />
                    </div>
                    <div className="p-2">
                        <label>email address</label>
                        <input type="email"
                               name="email"
                               className="form-control"
                               aria-describedby="emailHelp"
                               placeholder="Enter your email"
                               value={this.state.email}
                               onChange={this.handleInputChange}
                        />
                    </div>
                    <div className="p-2">
                        <label>password</label>
                        <input type="text"
                               name="password"
                               className="form-control"
                               placeholder="Enter your password"
                               value={this.state.password}
                               onChange={this.handleInputChange}
                        />
                    </div>
                    <div className="p-2">
                        <input type="submit"
                               value="submit"
                               className="btn btn-primary"
                        />
                    </div>
                </form>
            </div>
        );
    }
}

export default SignUp;