import React, {Component} from 'react';
import Piece from "./Piece";
import {newearPurple} from "../styles/colorPalette";


const styles = {
    text: {
        backgroundColor: 'white',
        opacity: 0.95
    }
};

const mapPiecesToLi = (piece) => (
    <li className="list-group-item row" key={piece.pieceId}>
        <Piece
            title={piece.title}
            composer={piece.composer}
            year={piece.year}
            duration={piece.duration}
            instruments={piece.instruments}
        />
    </li>
);

class Concert extends Component {
    constructor(props) {
        super(props);
        this.state = {
            concertId: props.concertId,
            concertTitle: props.concertTitle,
            concertDescription: props.concertDescription,
            posterLink: props.posterLink,
            posterAltText: props.posterAltText,
            pieces: props.pieces,
        };
        this.getTicketCount = this.getTicketCount.bind(this);
    }

    getTicketCount() {
        const cartItems = this.props.cartItems;
        if (cartItems) {
            let quantity = 0;
            cartItems.forEach(item => {
                if (item.itemName === this.state.concertTitle) {
                    quantity = item.itemQuantity;
                }
            });
            return quantity;
        }
        return 0;
    }

    render() {
        const pieceList = Array.from(this.state.pieces);
        return (
            <div className="col rounded" style={getStyle(this.props)}>
                <ConcertTitle concertTitle={this.state.concertTitle}/>
                <div className="row">
                    <div className="col-md-3">
                        <div className="row align-items-center justify-content-around">
                            <div className="col text-center">
                                <div className="rounded" style={styles.text}>
                                    tickets: {this.getTicketCount()}
                                </div>
                                <button className="btn btn-primary m-3"
                                        onClick={(event) => this.props.addToCart(event, this.state.concertId, this.state.concertTitle)}>
                                    add to cart
                                </button>
                            </div>
                        </div>
                    </div>
                    <div className="col col-md-9">
                        <ConcertDescription concertDescription={this.state.concertDescription}/>
                    </div>
                </div>
                <div className="row text-center" style={styles.text}>
                    <ul className="col">{pieceList.map(mapPiecesToLi)}</ul>
                </div>
            </div>
        );
    }
}

function ConcertTitle(props) {
    return (
        <div className="row text-center m-3">
            <h3 className="col p-3 rounded" style={styles.text}>
                {props.concertTitle}
            </h3>
        </div>
    );
}

function ConcertDescription(props) {
    return (
        <div className="row">
            <div className="col">
                <p className="p-3 rounded" style={styles.text}>
                    {props.concertDescription}
                </p>
            </div>
        </div>
    );
}

function getStyle(props) {
    return {
        color: `${newearPurple}`,
        backgroundImage: `url(${props.posterLink})`,
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover'
    };
}

export default Concert;