import React, {Component} from 'react';
import {newearGrey} from "../styles/colorPalette";

const styles = {
    details: {
        fontSize: '.85em',
    },
    pieces: {
        background: `${newearGrey}`,
        opacity: 0.96
    }
};

class Piece extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: props.title,
            composer: props.composer,
            year: props.year,
            duration: props.duration,
            instruments: props.instruments
        }
    }
    render() {
        const instruments = Array.from(this.state.instruments);
        return (
            <div className="col rounded p-3" style={styles.pieces}>
                <h4>{this.state.title}</h4>
                <h5>{this.state.composer}</h5>
                <p style={styles.details}>{this.state.year}</p>
                <p style={styles.details}>{this.state.duration}</p>
                <p>{instruments.join(", ")}</p>
            </div>
        );
    }
}

export default Piece;